import { FC } from 'react';
import classNames from 'classnames';
import {
  Draggable,
  DraggableProvided,
  DraggableStateSnapshot,
} from 'react-beautiful-dnd';

// Components
import { ContentListItem } from '../ContentList';
import { useDraggableInPortal } from './DraggableInPortal';

// Types
import { TestMetadata } from 'types';

export interface EditableContentListItemProps {
  /** Class name for Content List Item*/
  className?: string;
  /** Determines if the list item is an interactive object*/
  isInteractive?: boolean;
  /** Can list items be re-ordered */
  isReorderable?: boolean;
  //** Item id to be re-ordered */
  reorderId?: string;
  //** Item position to be re-ordered */
  reorderPosition?: number;
  /**  Shows reorderable disabled, useful for edit mode */
  isReorderableDisabled?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const EditableContentListItem: FC<EditableContentListItemProps> = ({
  children,
  className,
  isReorderable = false,
  reorderId,
  reorderPosition,
  isReorderableDisabled = false,
  testMetadata,
  ...otherProps
}) => {
  const renderDraggable = useDraggableInPortal();
  return (
    <Draggable
      isDragDisabled={!isReorderable || isReorderableDisabled}
      draggableId={reorderId || ''}
      index={reorderPosition || 0}
      {...testMetadata}
    >
      {renderDraggable(
        (provided: DraggableProvided, snapshot: DraggableStateSnapshot) => (
          <div
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            <ContentListItem
              {...otherProps}
              isReorderable={isReorderable}
              isReorderableDisabled={isReorderableDisabled}
              className={classNames(
                className,
                snapshot.isDragging &&
                  'sb-editable-content-list-item__dragging',
              )}
            >
              {children}
            </ContentListItem>
          </div>
        ),
      )}
    </Draggable>
  );
};

export default EditableContentListItem;
