import { FC } from 'react';
import { DragDropContext, Droppable, DropResult } from 'react-beautiful-dnd';

// Components
import { ContentList } from '../ContentList';

import { TestMetadata } from 'types';

export interface EditableContentListProps {
  /** Class name for Content List */
  className?: string;
  /** Can list items be re-ordered */
  isReorderable?: boolean;
  /** id of container for drag and drop */
  droppableId?: string;
  /** Event to fire on re-order */
  reorderAction?: (previousOrder: number, newOrder: number) => void;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const EditableContentList: FC<EditableContentListProps> = ({
  children,
  className,
  isReorderable = false,
  droppableId,
  reorderAction,
  testMetadata,
  ...otherProps
}) => {
  function onDragEnd(result: DropResult): void {
    if (!result.destination) {
      return;
    }

    if (result.destination.index === result.source.index) {
      return;
    }

    if (reorderAction)
      reorderAction(result.source.index, result.destination.index);
  }

  return (
    <DragDropContext onDragEnd={onDragEnd} {...testMetadata}>
      <Droppable
        isDropDisabled={!isReorderable}
        droppableId={droppableId || ''}
      >
        {(provided) => (
          <div ref={provided.innerRef} {...provided.droppableProps}>
            <ContentList {...otherProps} className={className}>
              {children}
              {provided.placeholder}
            </ContentList>
          </div>
        )}
      </Droppable>
    </DragDropContext>
  );
};

export default EditableContentList;
